import React, { useState } from 'react';
import PropTypes from 'prop-types';
import {
  AppBar,
  Badge,
  Box,
  Hidden,
  IconButton,
  Toolbar,
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import NotificationsIcon from '@material-ui/icons/NotificationsOutlined';
import InputIcon from '@material-ui/icons/Input';
import Tooltip from '@material-ui/core/Tooltip';
import { useDispatch } from 'react-redux';
import { useHistory } from "react-router-dom";

import { LOGOUT } from '../../reducers/constants/Auth';
import { LOADING_BACKTO_HOME } from '../../reducers/constants/Lazy';
const TopBar = ({
  onMobileNavOpen,
  ...rest
}) => {
  const [notifications] = useState([]);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleClickLogo = () => {
    dispatch({ type: LOADING_BACKTO_HOME })
    setTimeout(() => {
      history.push("/", "")
    }, 50);
  }
  return (

    // y l phn header trn cng
    <AppBar
      elevation={0}
      position="static"
      {...rest}
    >
      <Toolbar>
        <div onClick={handleClickLogo} style={{ cursor: "pointer" }}>
          <img src="/img/headTixLogo.png" alt="logo" style={{ height: 50 }} />
        </div>

        {/* 1 th div chim ht khong trng cn li d dn cc icon v 2 bn */}
        <Box flexGrow={1} />

        {/* ci icon chung v ng xut */}
        <Hidden mdDown>
          <IconButton color="inherit">
            <Badge
              badgeContent={notifications.length}
              color="primary"
              variant="dot"
            >
              <NotificationsIcon />
            </Badge>
          </IconButton>
          <Tooltip title="ng xut">
            <IconButton color="inherit" onClick={() => dispatch({ type: LOGOUT })}>
              <InputIcon />
            </IconButton>
          </Tooltip>
        </Hidden>

        {/* ci icon menu */}
        <Hidden lgUp>
          <IconButton
            color="inherit"
            // nu click th thc hin func ng m Nav c truyn vo t cha
            onClick={onMobileNavOpen}
          >
            <MenuIcon />
          </IconButton>
        </Hidden>
      </Toolbar>
    </AppBar>
  );
};

// bo li nu kiu d liu truyn vo props khng ng vi yu cu bn di
TopBar.propTypes = {
  className: PropTypes.string,
  onMobileNavOpen: PropTypes.func
};

export default TopBar;

