import React, { useEffect } from 'react';

import MovieIcon from '@material-ui/icons/Movie';
import PeopleAltIcon from '@material-ui/icons/PeopleAlt';
import PostAddIcon from '@material-ui/icons/PostAdd';
import { useLocation, useHistory } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Divider,
  Drawer,
  Hidden,
  List,
  Typography,
  makeStyles
} from '@material-ui/core';
import { useSelector } from 'react-redux';
import Tooltip from '@material-ui/core/Tooltip';

import NavItem from './NavItem';
import { FAKE_AVATAR } from '../../../constants/config';

const items = [
  {
    href: '/admin/movies',
    icon: MovieIcon,
    title: 'Qun l Phim'
  },
  {
    href: '/admin/users',
    icon: PeopleAltIcon,
    title: 'Qun l ngi dng'
  },
  {
    href: '/admin/showtimes',
    icon: PostAddIcon,
    title: 'To lch chiu'
  },
];

const useStyles = makeStyles(() => ({
  mobileDrawer: {
    width: 256
  },
  desktopDrawer: {
    width: 256,
    top: 64,
    height: 'calc(100% - 64px)'
  },
  avatar: {
    cursor: 'pointer',
    width: 64,
    height: 64
  }
}));

export default function NavBar({ onMobileClose, openMobile }) {
  const classes = useStyles();
  const location = useLocation();
  const history = useHistory();
  const { currentUser } = useSelector((state) => state.authReducer);

  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]);

  const user = {
    avatar: FAKE_AVATAR,
    jobTitle: 'Senior Developer',
    name: currentUser?.hoTen,
  };

  const handleUser = () => {
    history.push("/taikhoan")
  }


  // y l ni dung ct bn tri
  const content = (

    // ci ny l div  dn thnh ct
    <Box
      height="100%"
      display="flex"
      flexDirection="column"
    >
      {/* y l phn logo avatar user v tn user */}
      <Box
        // cn gia ct
        alignItems="center"
        display="flex"
        flexDirection="column"
        p={2} // padding 2
      >
        <Tooltip title="Thng tin ti khon">
          <Avatar
            className={classes.avatar}
            src={user.avatar}
            onClick={handleUser}
          />
        </Tooltip>
        <Typography
          className={classes.name}
          color="textPrimary"
          variant="h5"
        >
          {user.name}
        </Typography>
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {user.jobTitle}
        </Typography>
      </Box>
      <Divider />

      {/* y l phn menu la chn */}
      <Box p={2}>
        <List>
          {items.map((item) => (
            // NavItem hin th ra icon v title
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />
          ))}
        </List>
      </Box>
    </Box>
  );

  return (
    <>

      {/* y l giao din mobile */}
      <Hidden lgUp>
        <Drawer
          anchor="left"
          classes={{ paper: classes.mobileDrawer }}
          onClose={onMobileClose}
          open={openMobile} // ng m ty thuc vo bn click
          variant="temporary" // kiu temporary c mt lp ph m hin ra cho n khi bn chn xong th Drawer mi ng li
        >
          {content}
        </Drawer>

      </Hidden>
      {/* y l giao din desktop */}
      <Hidden mdDown>
        <Drawer
          anchor="left"
          classes={{ paper: classes.desktopDrawer }}
          open // lun lun hin Drawer
          variant="persistent" // kiu persistent khng c lp ph m khi hin drawer
        >
          {content}
        </Drawer>
      </Hidden>

    </>
  );
};

NavBar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

NavBar.defaultProps = {
  onMobileClose: () => { },
  openMobile: false
};

// export default NavBar;

